import React from 'react';
import { Button } from 'react-bootstrap';
import './css/ContentPageNine.css';
import Particles from "react-tsparticles";
function ContentPageNine() {
    return (
        <div className="containerNine">
   {/* <div style={{width:"1500px"}}>      
<h1 className="headingNine1">PROVIDE YOU THE HIGHEST QUALITY PRODUCTS<br/>THAT MEETS YOUR EXPECTATIONS</h1> 
<h5 className="headingNine2">Lorem, ipsum dolor sit amet consectetur adipisicing elit. </h5>
<Button className="btnNine">Discover More</Button>
</div>    */}
<Particles className="animation"
        id="tsparticles"
        options={{
          background: {
            color: {
              value: "#E0E0E0",
            },
          },
          fpsLimit: 60,
          interactivity: {
            detectsOn: "canvas",
            events: {
              onClick: {
                enable: true,
                mode: "push",
              },
              onHover: {
                enable: true,
                mode: "repulse",
              },
              resize: true,
            },
            modes: {
              bubble: {
                distance: 400,
                duration: 2,
                opacity: 0.8,
                size: 40,
              },
              push: {
                quantity: 4,
              },
              repulse: {
                distance: 200,
                duration: 0.4,
              },
            },
          },
          particles: {
            color: {
              value: "#0000",
            },
            links: {
              color: "#0000",
              distance: 150,
              enable: true,
              opacity: 0.5,
              width: 1,
            },
            collisions: {
              enable: true,
            },
            move: {
              direction: "none",
              enable: true,
              outMode: "bounce",
              random: false,
              speed: 6,
              straight: false,
            },
            number: {
              density: {
                enable: true,
                value_area: 800,
              },
              value: 80,
            },
            opacity: {
              value: 0.5,
            },
            shape: {
              type: "circle",
            },
            size: {
              random: true,
              value: 5,
            },
          },
          detectRetina: true,
        }}
      />
       </div>
       
    );
}

export default ContentPageNine;
