import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Fade from 'react-reveal/Fade';

const useStyles = makeStyles((theme) => ({
    root: {
      maxWidth: 200,
      position:"relative",
      top:"150px",
      textAlign:"center",
      left:"100px",
      marginRight:"60px"
    },
    media: {
        width:"100%",
      height: 0,
      paddingTop: "58.25%" ,
      paddingLeft:0,
      marginBottom:"10px",
    },
    expand: {
      transform: "rotate(0deg)",
      marginLeft: "auto",
      transition: theme.transitions.create("transform", {
        duration: theme.transitions.duration.shortest
      })
    },
    expandOpen: {
      transform: "rotate(180deg)"
    },
    avatar: {
      backgroundColor: red[500]
    },
    icon2:{
        fontFamily: 'Cabin Sketch',
        color: '#000000',
    },
    header1:{
        position:"relative",
        top:"50px",
        textAlign:"center",
        

    },
    header2:{
        position:"relative",
        top:"50px",
        textAlign:"center",
    },
    container1:{
        width:"100%",
position:"relative",
top:"700px",
justifyContent:"center",
backgroundColor:"#E0E0E0",
overflow: "hidden",
height: "calc(100vh - 16px)",
resize:"horizontal",

            },
            leaveImg:{
                width: "3%",
                transform: "rotate(45deg)",
                position:"relative",
                top:"50px",
                left:"930px",
            },
    
  }));
function ContentPageThree() {
    const classes = useStyles();

    return (
        <div className={classes.container1}>
           
            <h5 className={classes.header1} style={{ color: '#486A47',fontWeight: 'bold' }}>WHAT WE DO </h5>
            <h2 className={classes.header2} style={{ color: '#232B2B',fontWeight: 'bold',fontFamily: 'Cabin Sketch' }}>SERVICES WE OFFER</h2>
            <Fade bottom >
            <img src="images/leave.png" className={classes.leaveImg}/>
            </Fade>
            <Container>
            <Row>
            <Fade bottom delay={500}>
            <Card className={classes.root}>
            <Col>
      <CardHeader
        title="FRESH VEGETABLES"
        className={classes.icon2} 
      />
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          This impressive paella is a perfect party dish and a fun meal to cook
          together with your guests. Add 1 cup of frozen peas along with the
          mussels, if you like.
        </Typography>
       
      </CardContent>
      <CardMedia
        className={classes.media}
        image="/images/vegetables.jpg"
        title="Paella dish"
      />
      </Col>
    </Card>
    </Fade> 
    <Fade bottom delay={1500}>
    <Card className={classes.root}>
            <Col>
      <CardHeader
        title="FRESH VEGETABLES"
        className={classes.icon2} 
      />
      
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          This impressive paella is a perfect party dish and a fun meal to cook
          together with your guests. Add 1 cup of frozen peas along with the
          mussels, if you like.
        </Typography>
       
      </CardContent>
      <CardMedia
        className={classes.media}
        image="/images/vegetable1.jpg"
        title="Paella dish"
      />
      </Col>
    </Card>
</Fade>
<Fade bottom delay={2000}>
    <Card className={classes.root}>
            <Col>
      <CardHeader
        title="FRESH VEGETABLES"
        className={classes.icon2} 
      />
      
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          This impressive paella is a perfect party dish and a fun meal to cook
          together with your guests. Add 1 cup of frozen peas along with the
          mussels, if you like.
        </Typography>
      </CardContent>
      <CardMedia
        className={classes.media}
        image="/images/vegetable2.jpg"
        title="Paella dish"
      />
      </Col>
    </Card>
    </Fade>

    <Fade bottom delay={2500}>
    <Card className={classes.root}>
            <Col>
      <CardHeader
        title="FRESH VEGETABLES"
        className={classes.icon2} 
      />
     
      <CardContent>
        <Typography variant="body2" color="textSecondary" component="p">
          This impressive paella is a perfect party dish and a fun meal to cook
          together with your guests. Add 1 cup of frozen peas along with the
          mussels, if you like.
        </Typography>
      </CardContent>
      <CardMedia
        className={classes.media}
        image="/images/vegetable3.jpg"
        title="Paella dish"
      />
      </Col>
    </Card>
    </Fade>
    </Row>
    </Container>
    
        </div>
    );
}

export default ContentPageThree;