import React from 'react'
import './css/ContentPageTwo.css';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import {  Typography } from '@material-ui/core';
import Button from 'react-bootstrap/Button'

const useStyles = makeStyles((theme) => ({
    root: {
      display: 'block',
      flexWrap: 'wrap',
      '& > *': {
        
        width: "39%",
        height:"679px",
      position:"relative",
      bottom:"1738px",
      left:"1120px"
      },
    },
    paper:{
      paddingTop:"20px",
      paddingRight:"470px",
      paddingLeft:"20px",
      
    },
    icon1:{
      position:"relative",
      left:"120px",
      bottom:"55px",
      padding:"0px",
      
    },
    icon2:{
      position:"relative",
      left:"500px",
      bottom:"182px",
      padding:"0px",
    },
    typo:{
      position:"relative",
      
      bottom:"100px",
      padding:"0px",
      width:"290%",
     
    },
    btn:{
      position:"relative",
      marginTop:"-150px",
     marginLeft:"-12px",
      
    },
    container1:{
width:"100%",
backgroundColor:"red",
position:"relative",
top:"1200px",
right:"90px",
height:0,
    },
    typo1:{
      width:"285%",
    }
  }));
export default function ContentPageTwo() {
    const classes = useStyles();
    return (
        <div  className={classes.container1}>
            <img src="images/sphere.jpg" className="sphereImg"/>
            <img src="images/trackter.jpg" className="trackterImg"/>
            <img src="images/farmer.jpg" className="farmerImg"/>
            <img src="images/smartfarm.png" className="smartfarmImg"/>

            
            
            <div className={classes.root}>
     
      
      <Paper elevation={10} className={classes.paper}>
        
        <h6 style={{ color: '#486A47',fontWeight: 'bold' }}>ABOUT AGRICULTURE</h6>
        <h2 style={{ color: '#232B2B',fontWeight: 'bold',fontFamily: 'Cabin Sketch' }}>WE'RE LEADER IN </h2>
        <h2 style={{ color: '#232B2B',fontWeight: 'bold',fontFamily: 'Cabin Sketch'}}>AGRICULTURE MARKET </h2>
        <img src="images/leave.png" className="leaveImg"/>
        <Typography style={{fontFamily: 'Trebuchet MS',color: '#000000'}} className={classes.typo1} >Lorem ipsum dolor sit amet consectetur adipisicing elit. Accusantium natus mollitia animi nesciunt! Inventore incidunt praesentium voluptate, sunt totam explicabo quae ea accusamus corporis! Odio quibusdam eum placeat exercitationem illo?</Typography>
        <img src="images/farm.png" className="farmImg"/>
        <h4 style={{fontFamily: 'Trebuchet MS',color: '#000000'}} className={classes.icon1}>Growing Fruits <br/>and Vegetables</h4>
        <img src="images/agriculture.png" className="agricultureImg"/>
        <h4 className={classes.icon2} style={{fontFamily: 'Trebuchet MS',color: '#000000'}}>Tips for Ripening<br/> Your Fruits</h4>
        <Typography style={{fontFamily: 'Trebuchet MS',color: '#000000'}} className={classes.typo} >Lorem ipsum dolor, sit amet consectetur adipisicing elit. Dolores fuga iste commodi voluptatibus voluptatum, tenetur sunt deserunt earum accusamus? Corporis rerum a facere dicta. Repellat facilis iure sed magnam alias!</Typography>
        <Button  variant="warning text-dark" className={classes.btn}>Learn More</Button>
        </Paper>
    </div>

        </div>
    )
}
