import { Paper } from '@material-ui/core';
import React from 'react';
import './css/ContentPageSeven.css';
import Fade from 'react-reveal/Fade';
import ContentPageEight from './ContentPageEight';

function ContentPageSeven() {
    return (
        <>
        <ContentPageEight/>
        <div className="ContainerSeven">
            
           <h6 className="sevenHeader1">EXPLORE PROJECTS</h6>
           <h1 className="sevenHeader2">OUR RECENT PROJECTS</h1>
           <img src="images/leave.png" className="sevenLeave"/>
           <Fade bottom interval={1000}>
           <div style={{backgroundColor:"red",width:"15%",height:"50%",left:"460px",position:"relative"}}>
               <img src="images/project1.jpg" className="projectImg"/>
               <h1 className="sevenHeader3">ORGANIC <br/>SOLUTIONS</h1>
           </div>
           </Fade>

           <Fade bottom interval={1000}>
           <div style={{backgroundColor:"red",width:"15%",height:"50%",position:"relative",bottom:"440px",left:"760px"}}>
               <img src="images/project2.jpg" className="projectImg"/>
               <h1 className="sevenHeader3">ORGANIC <br/>SOLUTIONS</h1>
           </div>
           </Fade>

           <Fade bottom interval={1000}>
           <div style={{backgroundColor:"red",width:"15%",height:"50%",position:"relative",bottom:"880px",left:"1060px"}}>
               <img src="images/project3.jpg" className="projectImg"/>
               <h1 className="sevenHeader3">ORGANIC <br/>SOLUTIONS</h1>
           </div>
           </Fade>
        </div>
        </>
    );
}

export default ContentPageSeven;