import React from 'react';
import './css/ContentPageFour.css';
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import { Image } from "semantic-ui-react";
import Fade from 'react-reveal/Fade';

function ContentPageFour() {
    const responsive = {
        desktop: {
          breakpoint: { max: 3000, min: 1024 },
          items: 3,
          paritialVisibilityGutter: 60
        },
        tablet: {
          breakpoint: { max: 1024, min: 464 },
          items: 2,
          paritialVisibilityGutter: 50
        },
        mobile: {
          breakpoint: { max: 464, min: 0 },
          items: 1,
          paritialVisibilityGutter: 30
        }
      };
    const slideImages = [
        'images/vegetable1.jpg',
        'images/vegetable1.jpg',
        'images/vegetable1.jpg'
      ];
      const images = [
        "images/agLogo1.png",
        "images/agLogo2.png",
        "images/agLogo3.png",
        "images/agLogo4.png",
        "images/agLogo5.png",
      ];
    return (
        <div className="container1 ">
        <Carousel
      ssr
      infinite={true}
      partialVisbile
      autoPlay={ true }
      autoPlaySpeed={2000}
      itemClass="image-item"
      responsive={responsive}
    >
      {images.slice(0, 5).map(image => {
        return (
          <Image
            draggable={false}
            style={{ width: "30%", height: "90%",marginTop:"10px",filter: "contrast(0%)" }}
            src={image}
          />
        );
      })}
    </Carousel>
    <Fade left delay={1000}>
    <div className="container2">
    <img src="images/leave.png" className="newLeaf1"/>
    <h5 className="bestProduct">WE SELL BEST ARCHITECTURAL PRODUCTS</h5>
    <img src="images/leave.png" className="newLeaf2"/>
    </div>
    </Fade>
    <Fade right delay={1000}>
    <div className="container3">
    <img src="images/leave.png" className="newLeaf1"/>
    <h5 className="bestProduct">WE'VE 40YEARS EXPERIENCE IN FIELD</h5>
    <img src="images/leave.png" className="newLeaf2"/>
    </div>
    </Fade>
        </div>
    );
}

export default ContentPageFour;